import { DOGS } from "../actions";

const initialState = {
  dogs: [],
  temperaments: [],
  alldogs: [],
  details: [],
};

function rootReducer(state = initialState, action) {
  switch (action.type) {
    case DOGS:
      return {
        ...state,
        dogs: action.payload,
        alldogs: action.payload,
      };
      default: 
      return state
  }

}

export default rootReducer;
